import React from 'react';
import s from './Profile.module.css'
import MyPostsContainer from "./MyPosts/Post/MyPostsContainer";
import {PostsType, ProfileType} from "../../redux/profile-reducer";
import userPhoto from '../../assets/images/UserIcon.png'
import {Redirect} from "react-router-dom";
import ProfileStatus from "./MyPosts/ProfileStatus";
import CircularProgress from "@mui/material/CircularProgress";
import Box from "@mui/material/Box";

type ProfileInfoType = {
    profile: ProfileType | null
    status: string
    updateStatus: (userId: string) => void
}


const ProfileInfo = (props: ProfileInfoType) => {
    if (!props.profile) {
        return <Box sx={{display: 'flex'}}>
            <CircularProgress/>
        </Box>
        // return <div>Preloader</div>
    }


    return (
        <div>
            {/*<div>*/}
            {/*    <img*/}
            {/*        src='https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/a9e3440d-0f91-47a7-82c9-7c72df86c43d/ddzhn9j-59f0da47-6ed4-4e34-b732-aec2d19513d0.png/v1/fill/w_598,h_350,q_70,strp/harry_potter_and_the_chamber_of_secrets_fanart_07_by_vladislavpantic_ddzhn9j-350t.jpg?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..Y9jlhivjYT4XS4LVTbDlVe2ipynHrHPquQ1WsaRE_8s'/>*/}
            {/*</div>*/}
            <div>
                {props.profile.fullName}
            </div>
            <p>
                About me: {props.profile.aboutMe}
            </p>
            <div className={s.descriptionBlock}>
                <img
                    src={props.profile.photos?.large ? props.profile.photos.large : userPhoto}
                />
                <ProfileStatus status={props.status} updateStatus={props.updateStatus}/>
            </div>
            <div>
                LookingForAJobDescription: {props.profile.lookingForAJobDescription}
            </div>
        </div>
    )
}


interface ProfilePropsTypeNew {
    profile: ProfileType | null,
    post: PostsType[]
    messageForNewPost: string
    setUsersProfile: (profile: ProfileType) => void
    isAuth: boolean
    status: string
    updateStatus: (userId: string) => void
}


const Profile = (props: ProfilePropsTypeNew) => {

    if(!props.isAuth) return <Redirect to={'/login'}/>

    return (
        <div>
            <ProfileInfo
                profile={props.profile}
                status={props.status}
                updateStatus={props.updateStatus}
            />
            <MyPostsContainer/>
        </div>
    )
}

export default Profile